// AssessmentResults/index.js - VERSION REFACTORIS√âE avec composants g√©n√©riques

import { View, ScrollView } from "react-native";
import HeroCard from "../../../../components/ui/HeroCard";
import ContentSection from "../../../../components/ui/ContentSection";
import NavigationButtons from "../../../../components/exercise-common/NavigationButtons";
import createStyles from "./style";

/**
 * üèÜ AssessmentResults - Version Refactoris√©e avec composants g√©n√©riques
 * 200+ lignes ‚Üí 60 lignes (-70% de code)
 * Utilise HeroCard + ContentSection + NavigationButtons
 * 
 * @param {string} level - Niveau de l'√©valuation
 * @param {string} levelColor - Couleur du niveau
 * @param {object} userScore - Score utilisateur {correctAnswers, totalQuestions, percentage}
 * @param {function} onContinue - Fonction pour continuer
 * @param {function} onRetry - Fonction pour recommencer
 */
const AssessmentResults = ({
  level,
  levelColor = "#3b82f6",
  userScore,
  onContinue,
  onRetry,
}) => {
  const styles = createStyles(levelColor);

  // Fallback si pas de score
  if (!userScore) {
    return (
      <ScrollView style={styles.container} contentContainerStyle={styles.content}>
        {/* üéØ HERO - Titre simple */}
        <HeroCard 
          content="√âvaluation Termin√©e"
          fontSize={24}
          levelColor={levelColor}
          showUnderline
          backgroundColor="white"
        />

        {/* üìù MESSAGE SIMPLE */}
        <ContentSection
          title={`Niveau ${level}`}
          content="Votre √©valuation est termin√©e. Merci d'avoir particip√© !"
          levelColor={levelColor}
          backgroundColor="#f8fafc"
        />

        {/* üöÄ NAVIGATION */}
        <View style={styles.navigationContainer}>
          <NavigationButtons
            onNext={onContinue}
            disablePrevious
            disableNext={false}
            primaryColor={levelColor}
            isLast
            buttonLabels={{
              next: "Continuer",
              finish: "Continuer"
            }}
          />
        </View>
      </ScrollView>
    );
  }

  // Calculs pour l'affichage
  const scoreOutOf20 = Math.round((userScore.percentage / 100) * 20);
  const wrongAnswers = userScore.totalQuestions - userScore.correctAnswers;

  // Syst√®me de couleurs et messages bas√© sur performance
  const getPerformanceData = (scoreOut20) => {
    if (scoreOut20 >= 16) {
      return {
        title: "üèÜ Bravo champion !",
        subtitle: "Tu ma√Ætrises bien ton affaire !",
        color: "#16a34a",
        backgroundColor: "#f0fdf4",
        showRetry: false
      };
    } else if (scoreOut20 >= 12) {
      return {
        title: "üëå Pas mal du tout !",
        subtitle: "On est sur la bonne voie !",
        color: "#ea580c",
        backgroundColor: "#fff7ed",
        showRetry: false
      };
    } else if (scoreOut20 >= 8) {
      return {
        title: "üò¨ Hmm... on a vu mieux !",
        subtitle: "Allez, on remonte la pente !",
        color: "#dc2626",
        backgroundColor: "#fef2f2",
        showRetry: true
      };
    } else {
      return {
        title: "üî• Oula... c'est chaud !",
        subtitle: "Bon, on va pas se mentir, il faut bosser !",
        color: "#b91c1c",
        backgroundColor: "#fef2f2",
        showRetry: true
      };
    }
  };

  const performance = getPerformanceData(scoreOutOf20);

  return (
    <ScrollView 
      style={styles.container} 
      contentContainerStyle={styles.content}
      showsVerticalScrollIndicator={false}
    >
      {/* üéØ HERO - Score principal spectaculaire */}
      <HeroCard 
        content={`${scoreOutOf20}/20`}
        fontSize={56}
        levelColor={performance.color}
        showUnderline={false}
        backgroundColor={performance.backgroundColor}
        padding={32}
        subtitle={`${userScore.percentage.toFixed(1)}%`}
      />

      {/* üìä STATISTIQUES - ContentSection */}
      <ContentSection
        title="Statistiques d√©taill√©es"
        content={`‚úÖ ${userScore.correctAnswers} correctes\n‚ùå ${wrongAnswers} incorrectes\nüìä ${userScore.totalQuestions} questions au total`}
        levelColor="#6366f1"
        backgroundColor="white"
        isMonospace
      />

      {/* üé≠ MESSAGE DE PERFORMANCE */}
      <ContentSection
        title="Votre Performance"
        content={`${performance.title}\n\n${performance.subtitle}`}
        levelColor={performance.color}
        backgroundColor={performance.backgroundColor}
        icon={performance.title.split(' ')[0]} // R√©cup√®re l'emoji
      />

      {/* üè∑Ô∏è BADGE NIVEAU */}
      <ContentSection
        title={`√âvaluation Niveau ${level}`}
        content="√âvaluation termin√©e avec succ√®s !"
        levelColor={levelColor}
        backgroundColor="#f1f5f9"
        isItalic
      />

      {/* üöÄ NAVIGATION - Boutons d'action */}
      <View style={styles.navigationContainer}>
        {/* Bouton Retry conditionnel */}
        {performance.showRetry && onRetry && (
          <View style={styles.retryContainer}>
            <NavigationButtons
              onNext={onRetry}
              disablePrevious
              disableNext={false}
              primaryColor={performance.color}
              isLast={false}
              buttonLabels={{
                next: "Recommencer",
              }}
            />
          </View>
        )}

        {/* Bouton principal Continue */}
        <NavigationButtons
          onNext={onContinue}
          disablePrevious
          disableNext={false}
          primaryColor={levelColor}
          isLast
          buttonLabels={{
            next: "Continuer",
            finish: "Continuer"
          }}
        />
      </View>
    </ScrollView>
  );
};

export default AssessmentResults;